.header {

    .header__top-block {
        @include clr;
        .header__logo {
            float: left;
            width: width(1230, 220);
            padding: 15px 20px; }

        .search__form {
            float: left;
            width: width(1230, 650);
            padding: 15px 20px;

            .search {
                display: inline-block;
                .search__input {
                    border: 1px solid #dcdee0;
                    min-width: 550px;

                    input {
                        font-family: $font-common;
                        font-size: em(12, 13);
                        line-height: 28px;
                        padding-left: 15px;
                        width: 88%;
                        height: 30px; }

                    .search__btn {
                        background-color: #fff;
                        float: right;
                        &:last-of-type {
                            border-left: 1px solid #dcdee0;
                            border-right: 1px solid #dcdee0; } }

                    .icon-arrow-down_large {
                        padding: 15px;
                        background-position: -133px -106px;
                        width: 9px;
                        height: 7px; }
                    .icon-search {
                        padding: 15px;
                        background-position: 6px -104px;
                        width: 16px;
                        height: 16px; } } }

            .icon-settings {
                margin: 0 0 -4px 12px;
                background-position: -117px -52px;
                width: 16px;
                height: 16px;
                cursor: pointer; } }

        .user {
            float: right;
            padding: 15px 20px;
            width: width(1230, 360);
            @include clr;

            .user__icon {
                float: right;
                background-position: 0px 0px;
                width: 37px;
                height: 37px;
                cursor: pointer; }
            .user__login {
                float: right;
                font-size: em(12, 13);
                margin: 10px; }
            .user__logout {
                @extend %btn;
                border: 1px solid #dcdee0;
                float: right;
                margin-top: 8px; } } }

    .header__top-nav {
        border: 1px solid #dcdee0;

        ul {
            @include clr;
            .top-nav__item {
                float: left;
                padding: 12px 20px;

                &:first-of-type {
                    padding-left: 30px;
                    padding-right: 30px; }
                &:hover {
                    cursor: pointer; }
                &.active {
                    background-color: $bg-dark;
                    cursor: pointer;
                    .top-nav__item-text {
                        color: #fff; }
                    .icon-window {
                        background-position: -83px -81px; }
                    .icon-file {
                        background-position: -143px -48px; }
                    .icon-stack {
                        background-position: -78px -110px; }
                    .icon-star {
                        background-position: -5px -52px; } }


                .top-nav__item-text {
                    text-decoration: none;
                    text-transform: uppercase;
                    font-size: em(12, 13);
                    color: $dark-gray;
                    padding: 0 5px;
                    display: inline-block;
                    overflow: hidden; }
                .top-nav__item-extra {
                    padding: 0 5px;
                    margin-bottom: 1px;
                    background-color: #ff6c32;
                    border: none;
                    border-radius: 10px;
                    font-family: $font-regular;
                    font-size: em(10, 13);
                    font-weight: 400;
                    color: #fff;
                    display: inline-block;
                    overflow: hidden; }
                .icon {
                    width: 16px;
                    height: 16px; }
                .icon-window {
                    background-position: -117px 0px; }
                .icon-file {
                    background-position: -143px -72px; }
                .icon-stack {
                    background-position: -104px -110px; }
                .icon-star {
                    background-position: -41px -52px; } } } } }

