// buttons
%btn {
    font-family: $font-common;
    font-size: em(11, 13);
    font-weight: 600;
    color: $dark-gray;
    border: 1px solid $border-gray;
    border-radius: 45px;
    padding: 2px 12px;
    background-color: #fff; }

// links
%link {
    font-family: $font-bold;
    text-decoration: none;
    color: $dark-blue; }

// labels
%label {
    font-size: em(12, 13);
    display: table-cell;
    vertical-align: middle;
    padding: 0 15px; }
// icons
.icon {
    display: inline-block;
    background-image: url('../img/sprite.png');
    background-repeat: no-repeat;
    overflow: hidden;
    text-align: center; }

// selects
.multiple-select__wrapper {
    display: inline-flex;
    position: relative;
    height: 30px;
    @include clr;
    .multiple-select__placeholder {
        height: 100%;
        overflow: hidden;
        background-color: #fff;
        float: left;
        padding: 8px 15px 10px;
        font-family: $font-bold; }
    .multiple-select__list {
        position: absolute;
        top: 35px;
        left: 0;
        z-index: 999;
        width: 100%;
        background-color: #fff;
        padding: 10px 15px;
        border: 1px solid $border-gray;
        li {
            list-style: none;
            font-family: $font-regular;
            font-weight: 400;
            padding: 8px 0 10px;
            cursor: pointer; } }
    .multiple-select__switcher {
        height: 100%;
        float: left;
        overflow: hidden;
        .icon {
            display: block;
            width: 15px;
            height: 15px;
            background-color: #fff;
            cursor: pointer; }
        .icon-arrow-up {
            background-position: -16px -127px; }
        .icon-arrow-down {
            background-position: -33px -134px; } } }

