.content {
    .section__block {

        // BEGIN -- Content navigation

        .section__nav {
            @include clr;
            .nav__item {
                display: table;
                float: left;
                .label {
                    //display: inline-block
                    overflow: hidden; } }
            .icons {
                float: right;
                background-color: #fff;
                padding: 5px 10px;
                .icon {
                    border: 1px solid #fff;
                    width: 28px;
                    height: 28px;
                    padding: 5px;
                    cursor: pointer; }
                .icon-rect {
                    background-position: -26px -76px;
                    border: 1px solid #fff;
                    &:hover {
                        border: 1px solid $border-gray;
                        background-position: -112px -73px; }
                    &.active {
                        border: 1px solid $border-gray;
                        background-position: -112px -73px; } }
                .icon-lines {
                    background-position: -137px -18px;
                    &:hover {
                        border: 1px solid $border-gray;
                        background-position: -137px 6px; }
                    &.active {
                        border: 1px solid $border-gray;
                        background-position: -137px 6px; } } } }

        // END -- Content navigation


        // BEGIN -- Content table - general

        .section__table {
            background-color: #fff;
            width: 100%;

            .table__body {
                display: flex;
                flex-wrap: wrap;
                .row__wrapper {
                    position: relative;
                    border-bottom: 1px solid $border-gray;
                    &:hover {
                        .row {
                            .table__col-left {
                                .cell {
                                    .title {
                                        color: #fff; }
                                    .text {
                                        color: #fff; } } } } }
                    &.highlighted {
                        background-color: $hover-gray; }
                    .row {
                        display: flex;
                        cursor: pointer;

                        .table__col-left {
                            .icon-folder-single {
                                background-position: -85px 0px;
                                width: 22px;
                                height: 20px; }
                            .cell {
                                .title {
                                    @extend %link;
                                    font-size: em(15, 13); }
                                .text {
                                    font-size: em(10, 13);
                                    color: $light-gray; } } }

                        .table__col-right {
                            display: flex;
                            .cell__owner {
                                @extend %link;
                                font-size: 1em; }
                            .cell__date {
                                font-family: $font-bold;
                                font-size: 1em;
                                font-weight: 600; }
                            .cell__size {
                                font-family: $font-regular;
                                font-size: 1em;
                                font-weight: 400; } } }
                    .additional {
                        position: absolute;
                        z-index: 999;
                        right: 0;
                        background-color: $hover-gray;
                        opacity: 0.9;
                        .icon__wrapper {
                            height: 100%;
                            display: inline-block;
                            cursor: pointer;
                            &:hover {
                                background-color: $bg-dark; }
                            .icon {
                                display: inline-block;
                                overflow: hidden;
                                width: 16px;
                                height: 24px; } } } } }


 }            // END -- Content table - general


        // BEGIN -- Content table - rows

        .section__table[data-style="rows"] {
            padding: 0 10px 10px 10px;
            .table__head {
                display: flex;
                justify-content: space-between;
                border-bottom: 1px solid $border-gray;
                .table__col-left {
                    .head__elem {
                        font-size: em(10, 13);
                        padding: 8px 0;
                        margin-left: 40px;
                        .icon-arrow-down {
                            background-position: -33px -136px;
                            width: 7px;
                            height: 6px;
                            cursor: pointer;
                            margin-left: 7px; } } }
                .table__col-right {
                    .head__elem {
                        display: inline-block;
                        font-size: em(10, 13);
                        width: 68px;
                        padding: 8px 0; } } }


            .table__body {
                flex-direction: column;
                .row__wrapper {
                    &:last-of-type {
                        border-bottom: 1px solid #fff; }
                    .row {
                        flex-direction: row;
                        justify-content: space-between;
                        padding: 10px 0;

                        .table__col-left {
                            .icon-folder-single {
                                margin: 0px 8px 10px 8px; }
                            .cell {
                                display: inline-block; } }

                        .table__col-right {
                            justify-content: space-between;
                            .cell {
                                display: inline-block;
                                width: 70px; } } }
                    .additional {
                        top: 0;
                        height: 53px;
                        .icon__wrapper {
                            width: 65px;
                            padding: 15px;
                            .icon {
                                margin: 2px 10px 10px 10px; }
                            .icon-bell {
                                background-position: -57px -79px; }
                            .icon-sliders {
                                background-position: -52px -108px; }
                            .icon-star {
                                background-position: -5px -50px; }
                            .icon-settings {
                                background-position: -117px -24px; } } } } }


 }            // END -- Content table rows


        // BEGIN -- Content table - columns

        .section__table[data-style="columns"] {
            padding: 20px 10px 30px 10px;
            .table__head {
                height: 10px;
                background-color: #fff;
                & > * {
                    display: none; } }

            .table__body {
                flex-direction: row;
                justify-content: space-between;

                .row__wrapper {
                    cursor: pointer;
                    width: 240px;
                    padding: 20px 0 10px;
                    &:nth-of-type(-n+3) {
                        border-top: 1px solid $border-gray; }
                    &:hover {
                            border-bottom: 1px solid #606568; }
                    .row {
                        flex-direction: column;
                        height: 100%;

                        .table__col-left {
                            display: flex;
                            flex-direction: row;
                            flex: 1 0 auto;
                            .icon-folder-single {
                                margin: 0 10px; }
                            .cell__heading {
                                width: 80%; } }

                        .table__col-right {
                            flex-direction: row;
                            flex: 0 0 auto;
                            padding: 10px;
                            height: 30%;
                            .cell {
                                padding: 5px;
                                margin: 0 auto; } } }
                    .additional {
                        bottom: 0;
                        width: 100%;
                        height: 30%;
                        .icon__wrapper {
                            width: 25%;
                            text-align: center;
                            padding-top: 10px;
                            .icon-bell {
                                background-position: -57px -74px; }
                            .icon-sliders {
                                background-position: -52px -103px; }
                            .icon-star {
                                background-position: -5px -46px; }
                            .icon-settings {
                                background-position: -117px -19px; } } } }

 } } } }                // END -- Content table -columns
