.membership {
    .section__block {
        .section__sort {
            background-color: $bg-very-light;
            padding: 10px 20px;
            margin-top: 8px;
            text-align: center;
            .link {
                @extend %link;
                margin-right: 3px; } }
        .section__nav {
            margin: 15px 0 7px 0;
            @include clr;
            .nav__item {
                display: table;
                float: left;
                .label {
                    overflow: hidden; }
                .multiple-select__wrapper {
                    border: 1px solid $border-gray; } }
            .icons {
                float: right;
                display: inline-block;
                overflow: hidden;
                .icon {
                    @extend %btn;
                    width: 30px;
                    height: 30px;
                    padding: 5px;
                    cursor: pointer; }

                .icon-arrow-left {
                    background-position: -133px -88px; }
                .icon-arrow-right {
                    background-position: 12px -128px; } } }

        .section__content {
            .users {
                border: 1px solid $border-gray;
                padding: 12px 0;
                .user {
                    padding: 5px 12px;
                    @include clr;
                    &:hover {
                        background-color: $bg-light; }
                    .user__name {
                        float: left;
                        display: inline-block;
                        overflow: hidden;
                        padding: 5px 0 0 5px;
                        .link {
                            @extend %link; } }
                    .user__icon {
                        @extend .user__name;
                        display: inline-block;
                        overflow: hidden;
                        width: 25px;
                        height: 25px; }
                    .icon-user-off {
                        background-position: 0px -81px; }
                    .icon-user-on {
                        background-position: -85px -30px; }
                    .user__contact {
                        float: right;
                        display: inline-block;
                        overflow: hidden;
                        margin-top: 7px; }
                    .icon-mail {
                        background-position: -85px -59px;
                        width: 16px;
                        height: 12px;
                        cursor: pointer; }
                    .user__info {
                        @extend .user__contact;
                        display: inline-block;
                        overflow: hidden;
                        background-color: $bg-blue;
                        padding: 2px 5px;
                        margin: 5px 7px 0 0;
                        font-family: $font-regular;
                        font-size: em(10, 13);
                        font-weight: 400;
                        color: #fff; } } } } } }


